.button {
  font: inherit;
  font-size: 18px;
  line-height: 21px;
  padding: 16px 54px;
  background-color: #fcb500;
  border: none;
  border-radius: 7px;
  transition: all 0.3s ease; }
  .button:hover, .button:focus {
    background-color: #fcda28; }

html {
  box-sizing: border-box;
  min-width: 320px; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  font-family: "Rubik", "Helvetica", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px; }

.no-scroll {
  overflow: hidden; }

